.checkbox {
  background: #fff;
  border-radius: 5px;
}
.checkbox::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  opacity: 0;
  transition: 0.2s;
}
.checkbox__input {
  display: none;
}
.checkbox__input:checked + .checkbox__checked .icon-angle-down {
  scale: 1;
  opacity: 1;
}
.checkbox__checked {
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.checkbox__checked .icon-angle-down {
  color: #2b3769;
  font-size: 24px;
  transition: scale 0.2s ease-in-out;
  scale: 0;
}

.order-form__row {
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-bottom: 40px;
}
.order-form__row--tcl-order-page {
  color: #fff;
  font-size: 16px;
  display: flex;
  align-items: center;
}
.order-form__column {
  box-sizing: border-box;
  border-radius: 30px;
  align-self: center;
  flex: 0 1 268px;
  height: 58px;
  border: 1px solid #fff;
}
.order-form__column button:disabled {
  cursor: not-allowed;
  pointer-events: none;
  box-shadow: none;
  background-color: #c0c0c0;
  color: #797979;
}
.order-form__column button:disabled .icon {
  background: gray;
  opacity: 0.6px;
}
.order-form__column._brd-order input {
  color: #fff;
}
.order-form__column._brd-order .icon-arrow-right {
  color: #fff;
  font-size: 16px;
}
.order-form__column._brd-order .icon-arrow-right::before {
  font-weight: 500;
}
@media (min-width: 960.99px) {
  .order-form__column._brd-order .order-form__button:hover {
    border: 1px solid #fff;
  }
  .order-form__column._brd-order .order-form__button:hover .icon {
    transform: rotate(-45deg);
  }
  .order-form__column._brd-order .order-form__button:hover span {
    color: #fff;
  }
  .order-form__column._brd-order .order-form__button:hover::before {
    background: #2b3769;
  }
}
.order-form__column._brd-audit:not(:last-child) {
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.order-form__column._brd-audit input {
  color: #2c2c2c;
}
.order-form__column._brd-audit .order-form__button {
  background: #2b3769;
  border: 1px solid #2b3769;
}
.order-form__column._brd-audit .order-form__button .icon-arrow-right {
  color: #2b3769;
  font-size: 16px;
}
.order-form__column._brd-audit .order-form__button .icon-arrow-right::before {
  font-weight: 500;
}
.order-form__column._brd-audit .order-form__button .icon {
  background: #fff;
}
.order-form__column._brd-audit .order-form__button span {
  color: #fff;
}
@media (min-width: 960.99px) {
  .order-form__column._brd-audit .order-form__button:hover {
    border: 1px solid rgb(209, 209, 209);
  }
  .order-form__column._brd-audit .order-form__button:hover .icon {
    transform: rotate(-45deg);
    background: #2b3769;
  }
  .order-form__column._brd-audit .order-form__button:hover .icon-arrow-right::before {
    color: #fff;
  }
  .order-form__column._brd-audit .order-form__button:hover span {
    color: #000;
  }
  .order-form__column._brd-audit .order-form__button:hover::before {
    background: #fff;
  }
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .order-form__column {
    max-width: calc(215px + 53 * (100vw - 1440px) / 480);
    max-height: calc(48px + 10 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 960px) {
  .order-form__column {
    max-width: 268px;
    width: 100%;
    max-height: 58px;
  }
}
@media screen and (max-width: 690px) {
  .order-form__column {
    max-height: 48px;
  }
